import React from "react";
import { Row, Col, Slider, InputNumber, Popover, Radio } from 'antd';
import { SketchPicker } from 'react-color';
import "./index.scss"

const sliderData = [
    {
        label: "Horizontal Length",
        value: 10,
        max: 200,
        min: -200
    },
    {
        label: "Vertical Length",
        value: 10,
        max: 200,
        min: -200
    },
    {
        label: "Blur Radius",
        value: 5,
        max: 300,
        min: 0
    },
    {
        label: "Spread Radius",
        value: 0,
        max: 200,
        min: -200
    }
]

const colorData = [
    {
        lable: "Shadow Color",
        value: {
            r: "0",
            g: "0",
            b: "0",
            a: "1"
        }
    },
    {
        lable: "Background Color",
        value: {
            r: "255",
            g: "255",
            b: "255",
            a: "1"
        }
    },
    {
        lable: "Box Color",
        value: {
            r: "231",
            g: "166",
            b: "26",
            a: "1"
        }
    }
]
function BoxShadow() {

    const [data, setData] = React.useState(sliderData);

    const [color, setColor] = React.useState(colorData);

    const [opacity, setOpacity] = React.useState(0.75);

    const [mode, setMode] = React.useState("outline");

    const onChange = (value, idx) => {
        setData(data.map((item, index) => {
            if (index === idx) {
                return {
                    ...item,
                    value
                }
            } else {
                return { ...item }
            }
        }))
    }

    const changeColor = (rgba, idx) => {
        setColor(color.map((item, index) => {
            if (index === idx) {
                return {
                    ...item,
                    value: rgba
                }
            } else {
                return { ...item }
            }
        }))
    }

    return (
        <div className="app-boxshadow">
            <div className="app-boxshadow-left">
                <div className="app-boxshadow-setting">
                    {data?.map((item, index) => {
                        return (
                            <div className="app-boxshadow-item" key={index}>
                                <div className="app-boxshadow-item-label">{item.label}</div>
                                <Row>
                                    <Col span={16}>
                                        <Slider
                                            min={item.min}
                                            max={item.max}
                                            onChange={(value) => { onChange(value, index) }}
                                            value={typeof item.value === 'number' ? item.value : 0}
                                        />
                                    </Col>
                                    <Col span={2}>
                                        <InputNumber
                                            min={item.min}
                                            max={item.max}
                                            style={{ margin: '0 10px', width: "60px" }}
                                            value={item.value}
                                            onChange={(value) => { onChange(value, index) }}
                                        />
                                    </Col>
                                    <Col span={1}>
                                        <span className="app-boxshadow-px">px</span>
                                    </Col>
                                </Row>
                            </div>
                        )
                    })}
                </div>
                <div className="app-boxshadow-setting">
                    {
                        color?.map((item, index) => {
                            return (
                                <div key={index} className="app-block-item">
                                    <span>{item.lable}</span>
                                    <Popover
                                        content={
                                            <SketchPicker
                                                color={item.value}
                                                onChange={hexColor => {
                                                    changeColor(hexColor.rgb, index)
                                                }}
                                            />
                                        }
                                        trigger="click"
                                    >
                                        <div className="app-block-color" style={{ width: "32px", background: `rgba(${item.value.r},${item.value.g},${item.value.b},${item.value.a})` }}></div>
                                    </Popover>
                                </div>
                            )
                        })
                    }
                </div>
                <div className="app-boxshadow-setting app-boxshadow-opacity">
                    <div className="app-boxshadow-item">
                        <div className="app-boxshadow-item-label">Opacity</div>
                        <Row>
                            <Col span={16}>
                                <Slider
                                    min={0}
                                    max={1}
                                    step={0.01}
                                    onChange={(value) => { setOpacity(value) }}
                                    value={opacity}
                                />
                            </Col>
                            <Col span={2}>
                                <InputNumber
                                    min={0}
                                    max={1}
                                    style={{ margin: '0 10px', width: "60px" }}
                                    value={opacity}
                                    onChange={(value) => { setOpacity(value) }}
                                />
                            </Col>
                        </Row>
                        </div>
                        <div className="app-boxshadow-item" style={{paddingLeft: "4px"}}>
                        <Row>
                            <Radio.Group onChange={(e) => { setMode(e.target.value)}} value={mode}>
                                <Radio value={"outline"}>Outline</Radio>
                                <Radio value={"inset"}>Inset</Radio>
                            </Radio.Group>
                        </Row>
                    </div>
                </div>
            </div>
            <div className="app-boxshadow-right">
                <div className="app-container" style={{ background: `rgba(${color[1].value.r},${color[1].value.g},${color[1].value.b},${color[1].value.a})` }}>
                    <div className="app-box" style={{ background: `rgba(${color[2].value.r},${color[2].value.g},${color[2].value.b},${color[2].value.a})`, boxShadow: `${mode === "inset" ? "inset" : ""} ${data[0].value}px ${data[1].value}px ${data[2].value}px ${data[3].value}px rgba(${color[0].value.r},${color[0].value.g},${color[0].value.b},${opacity})` }}>
                        <div className="app-box-content">
                            <div>-webkit-box-shadow: {mode === "inset" ? "inset" : ""} {data[0].value}px {data[1].value}px {data[2].value}px {data[3].value}px rgba({color[0].value.r}, {color[0].value.g}, {color[0].value.b}, {opacity})</div>
                            <div>-moz-box-shadow: {mode === "inset" ? "inset" : ""} {data[0].value}px {data[1].value}px {data[2].value}px {data[3].value}px rgba({color[0].value.r}, {color[0].value.g}, {color[0].value.b}, {opacity})</div>
                            <div>box-shadow:  {mode === "inset" ? "inset" : ""} {data[0].value}px {data[1].value}px {data[2].value}px {data[3].value}px rgba({color[0].value.r}, {color[0].value.g}, {color[0].value.b}, {opacity})</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default BoxShadow;