import React from "react";
import { Tabs } from 'antd';
import BoxShadow from '../boxShadow';
import CssAnimate from "../cssAnimate";
import ResetCss from "../resetCss"
import FlexGenerator from "../flexGenerator";
import GridGenerator from "../gridGenerator";

function Content() {
    return (
        <div className="app-content">
            <Tabs
                defaultActiveKey="1"
                centered
                items={[
                    {
                        label: 'Css Animate',
                        key: '1',
                        children: <CssAnimate />
                    },
                    {
                        label: 'Flex Generator',
                        key: '2',
                        children: <FlexGenerator />
                    },
                    {
                        label: 'Grid Generator',
                        key: '3',
                        children: <GridGenerator />
                    },
                    {
                        label: 'Box Shadow',
                        key: '4',
                        children: <BoxShadow />
                    }, {
                        label: 'Reset Css',
                        key: '5',
                        children: <ResetCss />
                    }]}
            />
        </div>
    );
}

export default Content;