import React from "react";
import { message, Form, Select, Input, Tooltip, InputNumber, Popover } from "antd";
import {
    DeleteOutlined
} from '@ant-design/icons';
import "./index.scss"

const itemData = [{
    id: Math.random().toFixed(8),
    width: "100px",
    height: "100px",
    order: 1,
    alignSelf: "auto",
    flexBasis: "auto",
    flexGrow: 0,
    flexShrink: 1
}, {
    id: Math.random().toFixed(8),
    width: "100px",
    height: "100px",
    order: 1,
    alignSelf: "auto",
    flexBasis: "auto",
    flexGrow: 0,
    flexShrink: 1
}, {
    id: Math.random().toFixed(8),
    width: "100px",
    height: "100px",
    order: 1,
    alignSelf: "auto",
    flexBasis: "auto",
    flexGrow: 0,
    flexShrink: 1
}]

const layout = {
    labelCol: { span: 12 },
    wrapperCol: { span: 14 },
};

const layoutItem = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};

const { Option } = Select;

function FlexGenerator() {
    const [contaierForm] = Form.useForm();
    const [boxConfig, setBoxConfig] = React.useState({
        width: "100%",
        height: "420px",
        flexDirection: "row",
        flexWrap: "nowrap",
        justifyContent: "flex-start",
        alignContent: "stretch",
        alignItems: "stretch"
    })

    const [itemConfig, setItemConfig] = React.useState(itemData)

    React.useEffect(() => {
        contaierForm.setFieldsValue({ ...boxConfig });
        itemConfig.forEach((item, index) => {
            contaierForm.setFieldsValue({
                [`width-${index + 1}`]: item.width,
                [`height-${index + 1}`]: item.height,
                [`order-${index + 1}`]: item.order,
                [`alignSelf-${index + 1}`]: item.alignSelf,
                [`flexBasis-${index + 1}`]: item.flexBasis,
                [`flexGrow-${index + 1}`]: item.flexGrow,
                [`flexShrink-${index + 1}`]: item.flexShrink
            });
        })
    }, []);

    const copyText = (code) => {
        var range = document.createRange();
        range.selectNode(document.querySelector(code));
        var selection = window.getSelection();
        if (selection.rangeCount > 0) selection.removeAllRanges();
        selection.addRange(range);
        return document.execCommand('copy');
    }
    const tips = (status) => {
        if (status) {
            message.success('复制内容成功');
        } else {
            message.warning('复制失败，可选中内容手动复制');
        }
    }

    const onContainerChange = (params) => {
        setBoxConfig({ ...boxConfig, ...params })
        contaierForm.setFieldsValue({ ...boxConfig, ...params });
    }

    const changeConfig = (params) => {
        setItemConfig(itemConfig.map(item => {
            if (item.id === params.id) {
                return { ...item, ...params }
            } else {
                return { ...item }
            }
        }))
    }
    const addBox = () => {
        if (itemConfig?.length > 9) return
        itemConfig.push({
            id: Math.random().toFixed(8),
            width: "100px",
            height: "100px",
            order: 1,
            alignSelf: "auto",
            flexBasis: "auto",
            flexGrow: 0,
            flexShrink: 1
        })
        itemConfig.forEach((item, index) => {
            contaierForm.setFieldsValue({
                [`width-${index + 1}`]: item.width,
                [`height-${index + 1}`]: item.height,
                [`order-${index + 1}`]: item.order,
                [`alignSelf-${index + 1}`]: item.alignSelf,
                [`flexBasis-${index + 1}`]: item.flexBasis,
                [`flexGrow-${index + 1}`]: item.flexGrow,
                [`flexShrink-${index + 1}`]: item.flexShrink
            });
        })
        setItemConfig([...itemConfig])
    }

    const removeBox = (id) => {
        if (itemConfig?.length === 1) return
        setItemConfig([...itemConfig]?.filter(item => item.id !== id))
    }

    const setTooltip = (data) => {
        return (
            <Tooltip placement={data.placement || "top"} title={data.text}>
                <span>{data.label}</span>
            </Tooltip>
        )
    }

    const getItemBox = (item, index) => {
        return (<div className="app-item" key={item.id}>
            <div className="app-item-title">item-{index + 1} <DeleteOutlined className="app-item-del" onClick={() => { removeBox(item.id) }} /></div>
            <Form.Item {...layoutItem} name={`width-${index + 1}`} label={setTooltip({ label: "width", text: "项目的宽度" })}>
                <Input onChange={(e) => { changeConfig({ id: item.id, width: e.target.value }) }} />
            </Form.Item>
            <Form.Item {...layoutItem} name={`height-${index + 1}`} label={setTooltip({ label: "height", text: "项目的高度" })}>
                <Input onChange={(e) => { changeConfig({ id: item.id, height: e.target.value }) }} />
            </Form.Item>
            <Form.Item {...layoutItem} name={`order-${index + 1}`} label={setTooltip({ label: "order", text: "项目沿主轴方向的排列顺序，数值越小，排列越靠前" })}>
                <InputNumber onChange={(value) => { changeConfig({ id: item.id, order: value }) }} />
            </Form.Item>
            <Form.Item {...layoutItem} name={`alignSelf-${index + 1}`} label={setTooltip({ label: "align-self", text: "覆盖容器的align-items属性" })}>
                <Select
                    onChange={(value) => { changeConfig({ id: item.id, alignSelf: value }) }}
                >
                    <Option value="auto">{setTooltip({ label: "auto", text: "继承容器的align-items属性", placement: "right" })}</Option>
                    <Option value="stretch">{setTooltip({ label: "stretch", text: "如果项目height未设置或为auto，则拉伸至占满该行", placement: "right" })}</Option>
                    <Option value="flex-start">{setTooltip({ label: "flex-start", text: "侧轴方向上，起点对齐", placement: "right" })}</Option>
                    <Option value="flex-end">{setTooltip({ label: "flex-end", text: "侧轴方向上，终点对齐", placement: "right" })}</Option>
                    <Option value="center">{setTooltip({ label: "center", text: "侧轴方向上，居中对齐", placement: "right" })}</Option>
                    <Option value="baseline">{setTooltip({ label: "baseline", text: "侧轴方向上，基线对齐行", placement: "right" })}</Option>
                </Select>
            </Form.Item>
            <Form.Item {...layoutItem} name={`flexBasis-${index + 1}`} label={setTooltip({ label: "flex-basis", text: "主轴方向水平时，覆盖项目的width属性，主轴方向垂直时，覆盖项目的height属性" })}>
                <Input onChange={(e) => { changeConfig({ id: item.id, flexBasis: e.target.value }) }} />
            </Form.Item>
            <Form.Item {...layoutItem} name={`flexGrow-${index + 1}`} label={setTooltip({ label: "flex-grow", text: "项目沿主轴方向的伸长系数" })}>
                <InputNumber onChange={(value) => { changeConfig({ id: item.id, flexGrow: value }) }} />
            </Form.Item>
            <Form.Item {...layoutItem} name={`flexShrink-${index + 1}`} label={setTooltip({ label: "flex-shrink", text: "项目沿主轴方向的缩短系数" })}>
                <InputNumber onChange={(value) => { changeConfig({ id: item.id, flexShrink: value }) }} />
            </Form.Item>
        </div>)
    }

    return (
        <div className="app-flex">
            <Form {...layout} form={contaierForm} name="control-hooks">
                <div className="app-box" style={{
                    flexDirection: boxConfig["flexDirection"],
                    flexWrap: boxConfig["flexWrap"],
                    justifyContent: boxConfig["justifyContent"],
                    alignContent: boxConfig["alignContent"],
                    alignItems: boxConfig["alignItems"]
                }}>
                    {
                        itemConfig?.map((item, index) => (<div key={index} style={{
                            width: item.width,
                            height: item.height,
                            order: item.order,
                            alignSelf: item.alignSelf,
                            flexBasis: item.flexBasis,
                            flexGrow: item.flexGrow,
                            flexShrink: item.flexShrink
                        }}><Popover placement="bottom" title={""} content={getItemBox(item, index)}>
                                <span>item-{index + 1}</span>
                            </Popover></div>))

                    }
                </div>
                <div className="app-center">
                    <div className="app-title">配置 | Configs</div>
                    <div className="app-wrapper">
                        <div className="app-container app-item">
                            <div className="app-container-title">Container</div>
                            <Form.Item name="width" label={setTooltip({ label: "width", text: "容器的宽度" })}>
                                <Input disabled />
                            </Form.Item>
                            <Form.Item name="height" label={setTooltip({ label: "height", text: "容器的高度" })}>
                                <Input disabled />
                            </Form.Item>
                            <Form.Item name="flexDirection" label={setTooltip({ label: "flex-direction", text: "主轴的方向" })}>
                                <Select
                                    onChange={(value) => { onContainerChange({ flexDirection: value }) }}
                                >
                                    <Option value="row">{setTooltip({ label: "row", text: "主轴方向从左向右→", placement: "right" })}</Option>
                                    <Option value="row-reverse">{setTooltip({ label: "row-reverse", text: "主轴方向从右向左←", placement: "right" })}</Option>
                                    <Option value="column">{setTooltip({ label: "column", text: "主轴方向从上向下↓", placement: "right" })}</Option>
                                    <Option value="column-reverse">{setTooltip({ label: "column-reverse", text: "主轴方向从下向上↑", placement: "right" })}</Option>
                                </Select>
                            </Form.Item>
                            <Form.Item name="flexWrap" label={setTooltip({ label: "flex-wrap", text: "侧轴的方向，进而决定项目的换行方向" })}>
                                <Select
                                    onChange={(value) => { onContainerChange({ flexWrap: value }) }}
                                >
                                    <Option value="nowrap">{setTooltip({ label: "nowrap", text: "侧轴无方向，相当于不换行", placement: "right" })}</Option>
                                    <Option value="wrap">{setTooltip({ label: "wrap", text: "主轴方向水平时，侧轴方向从上向下↓，主轴方向竖直时，侧轴方向从左向右→，相当于顺序换行", placement: "right" })}</Option>
                                    <Option value="wrap-reverse">{setTooltip({ label: "wrap-reverse", text: "主轴方向水平时，侧轴方向从下向上↑，主轴方向竖直时，侧轴方向从右向左←，相当于逆序换行", placement: "right" })}</Option>
                                </Select>
                            </Form.Item>
                            <Form.Item name="justifyContent" label={setTooltip({ label: "justify-content", text: "项目沿主轴方向的对齐方式" })}>
                                <Select
                                    onChange={(value) => { onContainerChange({ justifyContent: value }) }}
                                >
                                    <Option value="flex-start">{setTooltip({ label: "flex-start", text: "主轴方向上，起点对齐", placement: "right" })}</Option>
                                    <Option value="flex-end">{setTooltip({ label: "flex-end", text: "主轴方向上，终点对齐", placement: "right" })}</Option>
                                    <Option value="center">{setTooltip({ label: "center", text: "主轴方向上，居中对齐", placement: "right" })}</Option>
                                    <Option value="space-between">{setTooltip({ label: "space-between", text: "主轴方向上，项目之间空隙相等", placement: "right" })}</Option>
                                    <Option value="space-around">{setTooltip({ label: "space-around", text: "主轴方向上，项目两侧空隙相等", placement: "right" })}</Option>
                                </Select>
                            </Form.Item>
                            <Form.Item name="alignContent" label={setTooltip({ label: "align-content", text: "项目沿侧轴方向的对齐方式，如果项目只有一行，则该属性无效" })}>
                                <Select
                                    onChange={(value) => { onContainerChange({ alignContent: value }) }}
                                >
                                    <Option value="stretch">{setTooltip({ label: "stretch", text: "如果项目height未设置或为auto，项目拉伸至占满侧轴", placement: "right" })}</Option>
                                    <Option value="flex-start">{setTooltip({ label: "flex-start", text: "侧轴方向上，起点对齐", placement: "right" })}</Option>
                                    <Option value="flex-end">{setTooltip({ label: "flex-end", text: "侧轴方向上，终点对齐", placement: "right" })}</Option>
                                    <Option value="center">{setTooltip({ label: "center", text: "侧轴方向上，居中对齐", placement: "right" })}</Option>
                                    <Option value="space-between">{setTooltip({ label: "space-between", text: "侧轴方向上，项目之间空隙相等", placement: "right" })}</Option>
                                    <Option value="space-around">{setTooltip({ label: "space-around", text: "侧轴方向上，项目两侧空隙相等", placement: "right" })}</Option>
                                </Select>
                            </Form.Item>
                            <Form.Item name="alignItems" label={setTooltip({ label: "align-items", text: "同一行项目沿侧轴方向的对齐方式，即项目在主轴方向上的垂直对齐方式" })}>
                                <Select
                                    onChange={(value) => { onContainerChange({ alignItems: value }) }}
                                >
                                    <Option value="flex-start">{setTooltip({ label: "flex-start", text: "侧轴方向上，起点对齐", placement: "right" })}</Option>
                                    <Option value="flex-end">{setTooltip({ label: "flex-end", text: "侧轴方向上，终点对齐", placement: "right" })}</Option>
                                    <Option value="center">{setTooltip({ label: "center", text: "侧轴方向上，居中对齐", placement: "right" })}</Option>
                                    <Option value="baseline">{setTooltip({ label: "baseline", text: "侧轴方向上，基线对齐", placement: "right" })}</Option>
                                    <Option value="stretch">{setTooltip({ label: "stretch", text: "如果项目height未设置或为auto，则拉伸至占满该行", placement: "right" })}</Option>
                                </Select>
                            </Form.Item>

                        </div>
                        {
                            itemConfig?.map((item, index) => {
                                return getItemBox(item, index)
                            })
                        }
                    </div>
                    <div className="app-plus" onClick={addBox}>+ Add A New Item</div>
                </div>
                <div className="app-html">
                    <div className="app-type">HTML</div>
                    <div className="app-btn" onClick={() => {
                        const isCopyed = copyText("#app-code-layout");
                        tips(isCopyed);
                    }}>一键复制</div>
                    <div id="app-code-layout" className="app-code-layout" >
                        {`<div class="app-box">
    ${itemConfig?.map((item, index) => (`<div class="app-item app-item-${index + 1}">item-${index + 1}</div>`)).join('\n    ')}
</div>
`}
                    </div>
                </div>
                <div className="app-css">
                    <div className="app-type">CSS</div>
                    <div className="app-btn" onClick={() => {
                        const isCopyed = copyText("#app-code-css");
                        tips(isCopyed);
                    }}>一键复制</div>
                    <div id="app-code-css" className="app-code-css" >
                        {`.app-box {
    display: flex;
    width: 100%;
    background: #262626;
    height: 420px;
    overflow: auto;
    flex-direction: ${boxConfig["flexDirection"]};
    flex-wrap: ${boxConfig["flexWrap"]};
    justify-content: ${boxConfig["justifyContent"]};
    align-content: ${boxConfig["alignContent"]};
    align-items: ${boxConfig["alignItems"]};
}`}

                        <br></br>
                        <br></br>

                        {`.app-item {
    background: #03a9f4;
}`}

                        <br></br>
                        <br></br>

                        {`${itemConfig.map((item, index) => {
                            return (`.app-item-${index + 1} {
    width: ${item.width},
    height: ${item.height},
    order: ${item.order},
    alignSelf: ${item.alignSelf},
    flexBasis: ${item.flexBasis},
    flexGrow: ${item.flexGrow},
    flexShrink: ${item.flexShrink}
}`)
                        }).join('\n\n')}`}
                    </div>
                </div>
            </Form>
        </div >
    );
}

export default FlexGenerator;