import React from "react";
import csstools from '../../csstools.svg';
import "./index.scss"

function Header() {
    return (
        <div className="app-header">
            <div className="app-logo">
                <img src={csstools} style={{ height: '65px' }} alt="logo" />
                <span className="app-text">tools</span>
            </div>
            <div className="app-title">The best <strong>CSS tools</strong> for web developer</div>
        </div>
    );
}

export default Header;